.containerC{
  display: grid;
  grid-template-columns: repeat(16,1fr);
  grid-template-rows: repeat(20,5%);
  background: rgb(228, 194, 243);
}

.headerC{
  grid-column-start:1;
  grid-column-end: 17;
  grid-row-start: 1;
  grid-row-end:4 ;
  display: flex;
  justify-content: space-around;
  border-radius: 10px;
  align-items: center
}

.header-img-wrapper{
  height: 100%;
}

.header-img{
  height:100%;
  position: relative;
  border-radius: 100%;
  margin: 0;
  border: solid red;  
  background: black;
}


.pageC {
  grid-column-start: 2;
  grid-column-end: 16;
  grid-row-start: 4;
  grid-row-end: 19;
  border-radius: 10px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  text-align: center;
  border:solid yellow;
}

.footerC {
  grid-column-start:1;
  grid-column-end: 17;
  grid-row-start: 19;
  grid-row-end:21 ;
  border-radius: 10px;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.footer-img-wrapper{
  height: 100%;
}

.footer-img{
  height: 100%;
}