.containerA{
  display: grid;
  grid-template-columns: repeat(16,1fr);
  grid-template-rows: repeat(20,5%);
  background: rgb(228, 194, 243);
}

.headerA{
  grid-column-start:1;
  grid-column-end: 17;
  grid-row-start: 1;
  grid-row-end:4 ;
  display: flex;
  justify-content: space-around;
  border-radius: 10px;
  align-items: center
}

.logoReact{
  height:100%;
  position: relative;
  border-radius: 100%;
  margin: 0;
}
.boy{
  height:100%;
  position: relative;
  border-radius: 100%;
  margin: 0;
  border: solid red;
  background: black;
}
.family{
  height:100%;
  position: relative;
  border-radius: 100%;
  margin: 0;
  border: solid red;
  background: black;
}

.logoReact:hover{
  /* filter: invert(27%) sepia(51%) saturate(2878%) hue-rotate(346deg) brightness(104%) contrast(97%); */
  animation: logoReact-spin infinite 5s linear;
  filter: invert(0.3) sepia(0.5) saturate(14.5) hue-rotate(72deg) brightness(0.88);
}
.boy:hover{
  animation: logoReact-spin infinite 5s linear;
  border: solid blue;
}
.family:hover{
  animation: logoReact-spin infinite 5s linear;
  border: solid blue;
}

@keyframes logoReact-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.icono-regreso{
  height:100%;
  position: relative;
}
.jaja{height: 100%;}
.boy-wrapper{height: 100%;}
.family-wrapper{height: 100%;}


.pageA {
  grid-column-start: 2;
  grid-column-end: 16;
  grid-row-start: 4;
  grid-row-end: 19;
  border:solid yellow;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  
}

.footerA {
  grid-column-start:1;
  grid-column-end: 17;
  grid-row-start: 19;
  grid-row-end:21 ;
  
  border-radius: 10px;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.boySchool{height: 100%;}
.familyHouse{height: 100%;}
.vscIcons{height: 100%;}
.img-wrapper{height: 100%;}
.footer-img{height: 100%;}

