.img-page{
  height:100%;
}

.containerB{
  display: grid;
  grid-template-columns: repeat(16,1fr);
  grid-template-rows: repeat(20,5%);
  background: rgb(228, 194, 243);
}

.headerB{
  grid-column-start: 1;
  grid-column-end: 17;
  grid-row-start: 1;
  grid-row-end: 4;
  display: flex;
  justify-content: space-around;
  border-radius: 10px;
  align-items: center;
}

.header-img-wrapper{
  height: 100%;
}

.header-img{
  height:100%;
  position: relative;
  border-radius: 100%;
  margin: 0;
  border: solid red;  
}

.header-img:hover{
  animation: logoReact-spin infinite 5s linear;
  border: solid blue;
}

.pageB{
  grid-column-start: 2;
  grid-column-end: 16;
  grid-row-start: 4;
  grid-row-end:19;
  display: flex;
  justify-content: center;
  align-items: center;
  border:solid yellow;
}

.footerB{
  grid-column-start: 1;
  grid-column-end: 17;
  grid-row-start: 19;
  grid-row-end: 21;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.footer-img-wrapper{
  height: 100%;
}

.footer-img{
  height: 100%;
  border-radius: 100%;
}

.footer-img:hover{
  border: solid black; 
}